import React, { useEffect, useState } from "react";
import axios from "axios";
import ViewTicket from "./modals/viewTicketsModal";
require("dotenv").config();

function ClientProject(props) {
  const [projectData, setProjectData] = useState("");

  useEffect(() => {
    console.log(props.match.params.id);
    // Replace the Authorization Token here.
    const Token = "Token " + localStorage.getItem("token");
    const options = {
      headers: {
        Authorization: Token,
      },
    };
    const url =
      "http://bug-tracker-11.herokuapp.com/api/user/project?client_id=" +
      props.match.params.id;
    // "/";
    axios.get(url, options).then(
      (response) => {
        console.log(response.data);
        setProjectData(response.data);
      },
      (error) => {
        console.log(error);
      }
    );
  }, []);

  return (
    <>
      <div className="container">
        {/* <ViewTicket projectId={props.match.params.id} /> */}
        {console.log("client name is")}
        {console.log(props)}
        <h3>
          <small>
            <b>{props.clientname}</b> Projects
          </small>
        </h3>

        <br />
        <table>
          <thead>
            <tr>
              <th>Project Id</th>
              <th>Project</th>
              <th>Description </th>
              <th>Company name</th>
              <th>Tickets</th>
              <th>View </th>
            </tr>
          </thead>
          <tbody>
            {projectData &&
              projectData.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>{item.id}</td>
                    <td>{item.name}</td>
                    <td>{item.description}</td>
                    <td>{item.company_name}</td>
                    <td>{item.tickets}</td>
                    <td>
                      {/* <View projectId={item.id} /> */}
                      <ViewTicket projectId={item.id} />
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default ClientProject;
