import React, { useEffect, useState, useRef } from "react";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";
import axios from "axios";
import contract from "../../build/contracts/BugTracker.json";
import web3 from "../../helper";
import "./Modals.css";
import qs from "qs";

function TitleDescForm(props) {
  var walletInstance = useRef(0);

  const [ticketinfo, Setticketinfo] = useState({
    title: "",
    description: "",
    priority: "",
    status: "Open",
    type: "",
  });

  useEffect(() => {
    console.log(props);
    try {
      if (!web3.eth.net.isListening()) {
        console.log("Not connected");
      } else {
        console.log("connected");
        InitializeContract();
      }
    } catch (e) {
      console.log("Exception is " + JSON.stringify(e));
    }
  }, []);

  // Initialize te contract
  const InitializeContract = () => {
    console.log("Initialize Contract!");

    const walletAbi = contract.abi;
    const walletContractAddress = contract.networks[3].address;
    walletInstance = new web3.eth.Contract(walletAbi, walletContractAddress);
    console.log(walletInstance);
  };

  const createTicket = (e) => {
    e.preventDefault();
    // Random blockchain data update
    addProjectSmartContract(
      e.target.first_name.value,
      e.target.description.value
    );

    const data = {
      title: ticketinfo.title,
      description: ticketinfo.description,
      priority: ticketinfo.priority,
      status: ticketinfo.status,
      type: ticketinfo.type,
    };

    console.log(data);
    console.log(props);
    const options = {
      headers: {
        "content-type": "application/x-www-form-urlencoded",
        Authorization: "Token " + localStorage.getItem("token"),
      },
    };
    const url =
      "http://bug-tracker-11.herokuapp.com/api/user/project/" +
      localStorage.getItem("projectId") +
      // props.projectId +
      "/ticket";
    console.log(url);
    axios.post(url, qs.stringify(data), options).then(
      (response) => {
        console.log("Response " + JSON.stringify(response));
      },
      (error) => {
        console.log(error);
      }
    );
  };

  // To retrieve Active account in metamask
  const metamaskAccount = () => {
    var promise = new Promise(function (resolve, reject) {
      web3.eth
        .getAccounts()
        .then((r) => {
          console.log("first account is " + r[0]);
          resolve(r[0]);
        })
        .catch((e) => {
          reject();
        });
    });
    return promise;
  };

  const addProjectSmartContract = async (name, description) => {
    await metamaskAccount().then((account) => {
      if (walletInstance.methods) {
        walletInstance.methods
          .addProject(name, description)
          .send({ from: account })
          .then((r) => {
            console.log("Created Successfully!");
            alert("Created Succesfully");
          })
          .catch((e) => {
            console.log(" Error is " + JSON.stringify(e));
          });
      }
    });
  };

  const title = (e) => {
    console.log(e.target.value);
    const value = { ...ticketinfo, title: e.target.value };
    Setticketinfo(value);
  };

  const description = (e) => {
    console.log(e.target.value);
    const value = { ...ticketinfo, description: e.target.value };
    Setticketinfo(value);
  };

  const typeFun = (e) => {
    console.log(e.target.value);
    const value = { ...ticketinfo, type: e.target.value };
    Setticketinfo(value);
  };

  // const statusFun = (e) => {
  //   console.log(e.target.value);
  //   const value = { ...ticketinfo, status: e.target.value };
  //   Setticketinfo(value);
  // };

  const priorityFun = (e) => {
    console.log(e.target.value);
    const value = { ...ticketinfo, priority: e.target.value };
    Setticketinfo(value);
    console.log(value);
  };

  return (
    <>
      <div class="row">
        <form
          class="col s12"
          onSubmit={(event) => {
            createTicket(event);
          }}
        >
          <div class="row">
            <div class="input-field col s12">
              <input
                placeholder="Title"
                id="first_name"
                type="text"
                class="validate"
                onChange={(e) => {
                  title(e);
                }}
              />
              {/* <label for="first_name">Title</label> */}
            </div>
            <div class="input-field col s12">
              <input
                placeholder="Description"
                id="description"
                type="text"
                class="validate"
                onChange={(e) => {
                  description(e);
                }}
              />
              {/* <label for="last_name">Description</label> */}
            </div>
            <div class="input-field col s12">
              <input
                list="type"
                placeholder="type:     (Feature/Bug/Other)"
                name="status"
                onChange={(e) => {
                  typeFun(e);
                }}
              />
              <datalist id="type">
                <option value="Feature/Request" />
                <option value="Bug/Error" />
                <option value="Others" />
              </datalist>
              {/* <label for="type">Type</label> */}
            </div>
            {/* <div class="input-field col s12">
              <input
                list="status"
                name="status"
                onChange={(e) => {
                  statusFun(e);
                }}
                // value={}
              />
              <datalist id="status">
                <option value="Open" />
                <option value="Closed" />
              </datalist>
              <label for="status">Status</label>
            </div> */}
            <div class="input-field col s12">
              <input
                list="Priority"
                name="status"
                placeholder="status:   (High/Medium/Low)"
                onChange={(e) => {
                  priorityFun(e);
                }}
                // value={}
              />
              <datalist id="Priority">
                <option value="High" />
                <option value="Medium" />
                <option value="Low" />
              </datalist>
              {/* <label for="Priority">Priority</label> */}
            </div>
            <div class="modal-close ">
              <input type="Submit" value="Submit" />
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

export default TitleDescForm;
