import React, { useEffect, useState } from "react";
import { Route, BrowserRouter as Router, Switch, Link } from "react-router-dom";
import axios from "axios";
require("dotenv").config();

function CompanyDashboard() {
  const [projectData, setProjectData] = useState("");

  useEffect(() => {
    // Replace the Authorization Token here.
    // const Token = "Token " + process.env.REACT_APP_API_KEY;
    // console.log(Token);
    const options = {
      headers: {
        Authorization: "Token " + localStorage.getItem("token"),
      },
    };
    axios
      .get("http://bug-tracker-11.herokuapp.com/api/company/dashboard", options)
      .then(
        (response) => {
          console.log(response.data);
          setProjectData(response.data);
        },
        (error) => {
          console.log(error);
        }
      );
  }, []);

  return (
    <>
      <div className="container">
        <br />
        <br />
        <table>
          <thead>
            <tr>
              <th>client id</th>
              <th>Client Name</th>
              <th>Projects count</th>
              <th>View</th>
            </tr>
          </thead>

          <tbody>
            {projectData &&
              Object.keys(projectData).map((item, index) => {
                var arr = item.split("-");
                const URL = "/Projects/" + arr[0];
                var clientName = arr[1] + " " + arr[2];

                return (
                  <tr>
                    <td>{arr[0]}</td>
                    <td>{arr[1] + " " + arr[2]}</td>
                    <td>{projectData[item]}</td>
                    <td>
                      {" "}
                      {console.log(`url ${URL}`)}
                      {console.log(clientName)}
                      <Link to={URL} clientname={clientName}>
                        {" "}
                        View{" "}
                      </Link>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default CompanyDashboard;
