import React, { useEffect, useState, useRef } from "react";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";
import axios from "axios";
import contract from "../../build/contracts/BugTracker.json";
import web3 from "../../helper";
import "./Modals.css";
import qs from "qs";

function ProjectDescForm() {
  var walletInstance = useRef(0);

  useEffect(() => {
    try {
      if (!web3.eth.net.isListening()) {
        console.log("Not connected");
      } else {
        console.log("connected");
        InitializeContract();
      }
    } catch (e) {
      console.log("Exception is " + JSON.stringify(e));
    }
  });

  // Initialize te contract
  const InitializeContract = () => {
    console.log("Initialize Contract!");

    const walletAbi = contract.abi;
    const walletContractAddress = contract.networks[3].address;
    walletInstance = new web3.eth.Contract(walletAbi, walletContractAddress);
    console.log(walletInstance);
  };

  const createProject = (e) => {
    e.preventDefault();
    addProjectSmartContract(
      e.target.first_name.value,
      e.target.description.value
    );
    const data = {
      name: e.target.first_name.value,
      description: e.target.description.value,
      company_name: e.target.company_name.value,
    };
    console.log(data);
    const options = {
      headers: {
        "content-type": "application/x-www-form-urlencoded",
        Authorization: "Token " + localStorage.getItem("token"),
      },
    };
    axios
      .post(
        "https://bug-tracker-11.herokuapp.com/api/user/project",
        qs.stringify(data),
        options
      )
      .then(
        (response) => {
          console.log("Response " + JSON.stringify(response));
        },
        (error) => {
          console.log(error);
        }
      );
  };

  // To retrieve Active account in metamask
  const metamaskAccount = () => {
    var promise = new Promise(function (resolve, reject) {
      web3.eth
        .getAccounts()
        .then((r) => {
          console.log("first account is " + r[0]);
          resolve(r[0]);
        })
        .catch((e) => {
          reject();
        });
    });
    return promise;
  };

  const addProjectSmartContract = async (name, description) => {
    await metamaskAccount().then((account) => {
      walletInstance.methods
        .addProject(name, description)
        .send({ from: account })
        .then((r) => {
          console.log("Created Successfully!");
          alert("Created Succesfully");
        })
        .catch((e) => {
          console.log(" Error is " + JSON.stringify(e));
        });
    });
  };

  return (
    <>
      <div class="row">
        <form
          class="col s12"
          onSubmit={(event) => {
            createProject(event);
          }}
        >
          <div class="row">
            <div class="input-field col s12">
              <input
                placeholder="Title"
                id="first_name"
                type="text"
                class="validate"
              />
              <label for="first_name">
                <b>Title</b>
              </label>
            </div>

            <div class="input-field col s12">
              <input
                id="description"
                // id="description"
                placeholder="Description"
                type="text"
                class="validate"
              />
              <label for="description">
                <b>Description</b>
              </label>
            </div>
            <div class="input-field col s12">
              {/* <label for="tp">
                <b>Choose company</b>
              </label> */}

              <input
                list="company_name"
                name="company_name"
                placeholder="Company Name"
                // onChange={(e) => {
                //   typeFun(e);
                // }}
              />
              <datalist id="company_name">
                <option value="jiit" />
                <option value="company 2" />
                <option value="company1" />
              </datalist>

              {/* <input list="type" name="status" />
              <datalist id="type">
                <option value="Feature" />
                <option value="Request" />
                <option value="Bug" />
                <option value="Error" />
                <option value="Others" />
              </datalist> */}
            </div>

            <div class="modal-close ">
              <input type="Submit" value="Submit" />
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

export default ProjectDescForm;
