import React, { Component } from "react";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";
import TitleDescForm from "./titleDescForm";
import "./Modals.css";
class CreateNewTicket extends Component {
  constructor(props) {
    super(props);
    console.log(props);
    this.state = {
      id: props.projectId,
      modalId: "modalTicket" + props.projectId,
    };
  }

  componentDidMount() {
    const options = {
      onOpenStart: () => {
        console.log("projectId props    " + this.props.projectId);
        console.log(
          "projectId localstorage    " + localStorage.getItem("projectId")
        );
      },
      onOpenEnd: () => {
        // console.log("Open End");
      },
      onCloseStart: () => {
        // console.log("Close Start");
      },
      onCloseEnd: () => {
        // console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "0%",
      endingTop: "10%",
    };
    M.Modal.init(this.Modal, options);
  }

  render() {
    return (
      <>
        <a
          className="waves-effect waves-light btn modal-trigger"
          data-target={this.state.modalId}
        >
          Create Ticket
        </a>

        <div
          ref={(Modal) => {
            this.Modal = Modal;
          }}
          id={this.state.modalId}
          className="modal"
        >
          <div className="modal-content">
            {/* {this.props.projectId} */}
            <TitleDescForm projectId={this.state.id} />
          </div>
          <div class="modal-footer">
            <a href="#" class="modal-close waves-effect waves-green btn-flat">
              Cancel
            </a>
          </div>
        </div>
      </>
    );
  }
}

export default CreateNewTicket;
