import React, { Component } from "react";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";
import axios from "axios";
import UpdateTicket from "./updateTicketModal";
import DetailTicket from "./viewTicketDetail";
require("dotenv").config();

class viewTicketModal extends Component {
  constructor(props) {
    super(props);
    console.log(props);
    this.getTicketInfo = this.getTicketInfo.bind(this);
    this.state = {
      ticketData: "",
      id: props.projectId,
    };
  }

  componentDidMount() {
    const options = {
      onOpenStart: () => {
        console.log(this.props.data);
        this.getTicketInfo();
      },
      onOpenEnd: () => {
        // console.log("Open End");
      },
      onCloseStart: () => {
        // console.log("Close Start");
      },
      onCloseEnd: () => {
        // console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%",
    };
    M.Modal.init(this.Modal, options);
  }

  getTicketInfo = () => {
    // Replace the Authorization Token here and put the project id dynamic
    const Token = "Token " + localStorage.getItem("token");
    const options = {
      headers: {
        Authorization: Token,
      },
    };
    console.log(this.props.projectId);
    const url =
      "http://bug-tracker-11.herokuapp.com/api/user/project/" +
      this.props.projectId +
      "/ticket";
    axios.get(url, options).then(
      (response) => {
        console.log(response.data);
        this.setState({ ticketData: response.data });
      },
      (error) => {
        console.log(error);
      }
    );
  };

  render() {
    return (
      <>
        <a
          className="waves-effect waves-light btn modal-trigger"
          data-target="Modal1"
        >
          View
        </a>

        <div
          ref={(Modal) => {
            this.Modal = Modal;
          }}
          id="Modal1"
          className="modal"
        >
          <div className="modal-content">
            <table>
              <thead>
                <tr>
                  <th>Title</th>
                  <th>Description</th>
                  <th>Status</th>
                  <th>View</th>
                  <th>Update</th>
                </tr>
              </thead>

              <tbody>
                {this.state.ticketData &&
                  this.state.ticketData.map((item, index) => {
                    return (
                      <tr>
                        <td>{item.title}</td>
                        <td>{item.description}</td>
                        <td>{item.status}</td>
                        <td>
                          <DetailTicket
                            // projectId={this.props.projectId}
                            projectId={item.project_id}
                            ticketId={item.id}
                          />
                        </td>
                        <td>
                          {" "}
                          <UpdateTicket
                            ticketTitle={item.title}
                            projectId={item.project_id}
                            ticketId={item.id}
                          />
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <a href="#" class="modal-close waves-effect waves-green btn-flat">
              Cancel
            </a>
          </div>
        </div>
      </>
    );
  }
}

export default viewTicketModal;
