import React, { Component } from "react";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";
import axios from "axios";
import contract from "../../../build/contracts/BugTracker.json";
import web3 from "../../../helper";
import qs from "qs";
require("dotenv").config();

class UpdateTicket extends Component {
  constructor(props) {
    super(props);
    console.log(props);

    this.getTicketData = this.getTicketData.bind(this);
    this.initializeDropdown = this.initializeDropdown.bind(this);

    this.priorityFun = this.priorityFun.bind(this);
    this.updateTicketAxiosCall = this.updateTicketAxiosCall.bind(this);

    this.state = {
      ticketInfo: "",
      value: "coconut",
      modalID: "modalId" + this.props.ticketId,
    };
  }

  componentDidMount() {
    const options = {
      onOpenStart: () => {
        console.log(this.props.data);
      },
      onOpenEnd: () => {
        // console.log("Open End");
      },
      onCloseStart: () => {
        // console.log("Close Start");
      },
      onCloseEnd: () => {
        console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%",
    };
    M.Modal.init(this.Modal, options);
    // this.initializeDropdown();
    this.getTicketData();
    this.checkWeb3();
  }

  checkWeb3 = () => {
    try {
      if (!web3.eth.net.isListening()) {
        console.log("Not connected");
      } else {
        console.log("connected");
        this.InitializeContract();
      }
    } catch (e) {
      console.log("Exception is " + JSON.stringify(e));
    }
  };

  InitializeContract = () => {
    console.log("Initialize Contract!");

    const walletAbi = contract.abi;
    const walletContractAddress = contract.networks[3].address;
    this.walletInstance = new web3.eth.Contract(
      walletAbi,
      walletContractAddress
    );
    console.log(this.walletInstance);
  };

  initializeDropdown = () => {
    document.addEventListener("DOMContentLoaded", function () {
      var elems = document.querySelectorAll("select");
      var options = {};
      var instances = M.FormSelect.init(elems, options);
    });
  };

  priorityFun = (e) => {
    console.log(e.target.value);
    const value = { ...this.state.ticketInfo, priority: e.target.value };
    this.setState({ ticketInfo: value });
    console.log(value);
  };

  descriptionFun = (e) => {
    console.log(e.target.value);
    const value = { ...this.state.ticketInfo, description: e.target.value };
    this.setState({ ticketInfo: value });
    console.log(value);
  };

  statusFun = (e) => {
    console.log(e.target.value);
    const value = { ...this.state.ticketInfo, status: e.target.value };
    this.setState({ ticketInfo: value });
    console.log(value);
  };

  typeFun = (e) => {
    console.log(e.target.value);
    const value = { ...this.state.ticketInfo, type: e.target.value };
    this.setState({ ticketInfo: value });
    console.log(value);
  };

  updateFun = () => {
    console.log("Update");
    console.log(this.state.ticketInfo);
    this.modifyTicketSmartContract(
      "0xf28cca91f1f03ba903CE603003Bf4e7E12a40407",
      0,
      this.state.ticketInfo.title,
      this.state.ticketInfo.description,
      this.state.ticketInfo.priority,
      this.state.ticketInfo.status,
      this.state.ticketInfo.type,
      this.state.ticketInfo.CreatedDate,
      this.state.ticketInfo.users
    );
    this.updateTicketAxiosCall();
  };

  updateTicketAxiosCall = () => {
    const data = {
      title: this.state.ticketInfo.title,
      description: this.state.ticketInfo.description,
      priority: this.state.ticketInfo.priority,
      status: this.state.ticketInfo.status,
      type: this.state.ticketInfo.type,
    };
    console.log(data);
    const options = {
      headers: {
        Authorization: "Token " + localStorage.getItem("token"),
      },
    };
    const url =
      "http://bug-tracker-11.herokuapp.com/api/user/project/" +
      this.props.projectId +
      "/ticket/" +
      this.props.ticketId +
      "/";
    axios.put(url, data, options).then(
      (response) => {
        console.log("Response " + JSON.stringify(response));
      },
      (error) => {
        console.log(error);
      }
    );
  };
  // To retrieve Active account in metamask
  metamaskAccount = () => {
    var promise = new Promise(function (resolve, reject) {
      web3.eth
        .getAccounts()
        .then((r) => {
          console.log("first account is " + r[0]);
          resolve(r[0]);
        })
        .catch((e) => {
          reject();
        });
    });
    return promise;
  };

  modifyTicketSmartContract = async (
    client,
    ticketNumber,
    title = "title",
    description = "desc",
    priority = "prio",
    status = "status",
    type = "type",
    createdDate = "date",
    users = "users"
  ) => {
    await this.metamaskAccount().then((account) => {
      this.walletInstance.methods
        .modifyTicket(
          client,
          ticketNumber,
          title,
          description,
          priority,
          status,
          type,
          createdDate,
          users
        )
        .send({ from: account })
        .then((r) => {
          console.log("Created Successfully!");
          alert("Created Succesfully");
        })
        .catch((e) => {
          console.log(" Error is " + JSON.stringify(e));
        });
    });
  };

  getTicketData = () => {
    // Replace the Authorization Token here and put the project id dynamic
    // const Token = "Token " + process.env.REACT_APP_API_KEY
    const options = {
      headers: {
        Authorization: "Token " + localStorage.getItem("token"),
      },
    };

    console.log(this.props.projectId);
    const url =
      "http://bug-tracker-11.herokuapp.com/api/user/project/" +
      this.props.projectId +
      "/ticket/" +
      this.props.ticketId +
      "/";
    axios.get(url, options).then(
      (response) => {
        console.log(response.data);
        this.setState({ ticketInfo: response.data });
        //   this.setState({ticketData: response.data});
      },
      (error) => {
        console.log(error);
      }
    );
  };

  render() {
    const item = this.props.data;
    return (
      <>
        <a
          className="waves-effect waves-light btn modal-trigger"
          data-target={this.state.modalID}
        >
          Info
        </a>

        <div
          ref={(Modal) => {
            this.Modal = Modal;
          }}
          id={this.state.modalID}
          className="modal"
        >
          <div className="modal-content">
            {this.state.ticketInfo && (
              <table>
                <thead></thead>

                <tbody>
                  <tr>
                    <td>Id</td>
                    <td>{this.state.ticketInfo.id}</td>
                  </tr>
                  <tr>
                    <td>Title</td>
                    <td>{this.state.ticketInfo.title}</td>
                  </tr>

                  <tr>
                    <td>Description</td>
                    <td>
                      <input
                        list="description"
                        onChange={(e) => {
                          this.descriptionFun(e);
                        }}
                        value={this.state.ticketInfo.description}
                      />
                      <datalist id="description">
                        <option value="Add Description detailed description" />
                      </datalist>
                    </td>
                  </tr>

                  <tr>
                    <td>Priority</td>
                    <td>
                      <input
                        list="priority"
                        onChange={(e) => {
                          this.priorityFun(e);
                        }}
                        value={this.state.ticketInfo.priority}
                      />
                    </td>
                    <datalist id="priority">
                      <option value="Low" />
                      <option value="Medium" />
                      <option value="High" />
                    </datalist>
                  </tr>
                  <tr>
                    <td>Status</td>
                    <td>
                      <input
                        list="status"
                        name="status"
                        onChange={(e) => {
                          this.statusFun(e);
                        }}
                        value={this.state.ticketInfo.status}
                      />
                      <datalist id="status">
                        <option value="Open" />
                        <option value="Closed" />
                      </datalist>
                    </td>
                  </tr>
                  <tr>
                    <td>Type</td>
                    <td>
                      <input
                        list="type"
                        name="status"
                        onChange={(e) => {
                          this.typeFun(e);
                        }}
                        value={this.state.ticketInfo.type}
                      />
                      <datalist id="type">
                        <option value="Feature/Request" />

                        <option value="Bug/Error" />

                        <option value="Others" />
                      </datalist>
                    </td>
                  </tr>
                  <tr>
                    <td>CreatedDate</td>
                    <td>{this.state.ticketInfo.CreatedDate}</td>
                  </tr>
                  <tr>
                    <td>Project Id</td>
                    <td>{this.state.ticketInfo.project_id}</td>
                  </tr>
                </tbody>
              </table>
            )}
          </div>
          <div class="modal-footer">
            <a
              href="#"
              class="modal-close waves-effect waves-green btn-flat"
              onClick={this.updateFun}
            >
              Update
            </a>
            <a href="#" class="modal-close waves-effect waves-green btn-flat">
              Close
            </a>
          </div>
        </div>
      </>
    );
  }
}

export default UpdateTicket;
