import React, { Component } from "react";
import "./App.css";
import Front from "./Front.js";
import Chart from "./Chart.js";
import { Route, BrowserRouter as Router, Switch, Link } from "react-router-dom";
import App_client from "./App_client";
import App_company from "./App_company";

import Login from "./components/login.js";
import SignUp from "./components/signup.js";
import ProjectAdd from "./components/projectsAdd.js";
import Logout from "./components/logout.js";
import clientDashboard from "./clientDashboard";
import companyDashboard from "./companyDashboard/companyDashboard";
import Home from "./components/Home";
import ProtectedRoute from "./ProtectedRoute";
import ClientProject from "./companyDashboard/ProjectsList/ProjectsList";

// import Header from "./components/header.js";
import contract from "./build/contracts/BugTracker.json";
import web3 from "./helper.js";
import axios from "axios";

class App extends Component {
  constructor(props) {
    super(props);
    const token = localStorage.getItem("token");
    let loggedIn = true;

    if (token == null) {
      loggedIn = false;
    }

    this.state = {
      loggedIn,
    };
  }

  isLoggedin = (bool) => {
    console.log("isLoggedin value is " + bool);
    this.setState({ loggedIn: bool });
  };

  logout = () => {
    this.setState({ loggedIn: false });
    console.log("Logout getting called!");
  };

  render() {
    return (
      <>
        <Router>
          <div className="App">
            <nav class="teal lighten-2">
              <div class="nav-wrapper">
                <a
                  href="#"
                  class="brand-logo"
                  style={{ fontFamily: "Georgia, serif" }}
                >
                  <Link to="/">&nbsp;BugTracker</Link>
                </a>
                <ul id="nav-mobile" class="right hide-on-med-and-down">
                  {this.state.loggedIn ? (
                    <li>
                      {" "}
                      <Link to="/logout">Logout</Link>
                    </li>
                  ) : (
                    <li>{/* <Link to="/">Login</Link> */}</li>
                  )}
                </ul>
              </div>
            </nav>
          </div>

          <Switch>
            <Route exact path="/chart" component={Chart} />
            <Route exact path="/" component={Front} />
            <Route path="/signup" component={SignUp} />
            {/* <Route path="/project" component={ProjectAdd} /> */}
            <Route
              path="/login"
              exact
              render={() => <Login isLoggedin={this.isLoggedin} />}
            />
            <Route
              path="/logout"
              render={(props) => <Logout {...props} logout={this.logout} />}
            />
            <Route path="/App_client" component={App_client} />
            <Route path="/App_company" component={App_company} />
            <ProtectedRoute
              exact
              path="/clientDashboard"
              component={clientDashboard}
            />
            <ProtectedRoute
              exact
              path="/companyDashboard"
              component={companyDashboard}
            />
            {/* <Route
              path="/"
              exact
              render={() => <Login isLoggedin={this.isLoggedin} />}
            /> */}
            <Route exact path="/Projects/:id" component={ClientProject} />
            <Route component={NoMatch} />
          </Switch>
        </Router>
      </>
    );
  }
}

function NoMatch() {
  return <div>NoMatch</div>;
}

export default App;

