import React, { useEffect, useState } from "react";
import CreateNewProject from "./modals/createNewProjectModal";
import View from "./modals/viewModal";
import axios from "axios";

function ClientDashboard() {
  const [projectData, setProjectData] = useState("");

  useEffect(() => {
    // Replace the Authorization Token here.
    const options = {
      headers: {
        Authorization: "Token " + localStorage.getItem("token"),
      },
    };
    axios
      .get("https://bug-tracker-11.herokuapp.com/api/user/project ", options)
      .then(
        (response) => {
          console.log(response.data);
          setProjectData(response.data);
        },
        (error) => {
          console.log(error);
        }
      );
  }, []);

  // RedirectFun = () => {
  //   console.log("Redirect");
  //   window.location.href = "/chart";
  // };

  return (
    <>
      <div className="container">
        <br />
        <CreateNewProject /> &nbsp;
        <a
          onClick={() => (window.location.href = "/chart")}
          className="waves-effect waves-light btn modal-trigger"
        >
          Charts
        </a>
        <br />
        <table>
          <thead>
            <tr>
              <th>Project</th>
              <th>Description </th>
              <th>Company name</th>
              <th>Tickets</th>
              <th>View </th>
            </tr>
          </thead>

          <tbody>
            {projectData &&
              projectData.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>{item.name}</td>
                    <td>{item.description}</td>
                    <td>{item.company_name}</td>
                    <td>{item.tickets}</td>
                    <td>
                      <View projectId={item.id} />
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default ClientDashboard;
