import React, { Component } from "react";
import "./App.css";
import Login from "./CompanyAuth/login";
import SignUp from "./CompanyAuth/signup.js";
// import ProjectAdd from "./components/projectsAdd.js";
import Logout from "./CompanyAuth/logout.js";
import clientDashboard from "./clientDashboard";
import companyDashboard from "./companyDashboard/companyDashboard";
import Home from "./components/Home";
import ProtectedRoute from "./ProtectedRoute";
import ClientProject from "./companyDashboard/ProjectsList/ProjectsList";
// import Header from "./components/header.js";
import { Route, BrowserRouter as Router, Switch, Link } from "react-router-dom";
import contract from "./build/contracts/BugTracker.json";
import web3 from "./helper.js";
import axios from "axios";

class App extends Component {
  constructor(props) {
    super(props);
    const token = localStorage.getItem("token");
    let loggedIn = true;

    if (token == null) {
      loggedIn = false;
    }

    this.state = {
      loggedIn,
    };
  }

  componentDidMount() {
    try {
      if (!web3.eth.net.isListening()) {
        console.log("Not connected");
      } else {
        console.log("connected");
        this.InitializeContract();
      }
    } catch (e) {
      console.log("Exception is " + JSON.stringify(e));
    }
  }

  // Initialize te contract
  InitializeContract = () => {
    console.log("Initialize Contract!");

    const walletAbi = contract.abi;
    const walletContractAddress = contract.networks[3].address;
    const walletInstance = new web3.eth.Contract(
      walletAbi,
      walletContractAddress
    );
    console.log(walletInstance);
  };

  // localStorage.getItem("token")

  isLoggedin = (bool) => {
    console.log("isLoggedin value is " + bool);
    this.setState({ loggedIn: bool });
  };

  logout = () => {
    this.setState({ loggedIn: false });
    console.log("Logout getting called!");
  };

  render() {
    let { loggedIn } = this.state;
    return (
      <>
        <Router></Router>
        <Login isLoggedin={this.isLoggedin} />
      </>
    );
  }
}

function NoMatch() {
  return <div>NoMatch</div>;
}

export default App;
